// Base Flip Boxes
.flip-box {
  // 04 Split
  &__split {
    &-img {
      background-color: $bg-shop-base-color-1;
    }

    .old-price {
      color: $text-base-color-1;
    }

    &-actions {
      .add-cart,
      .add-wishlist,
      .add-compare,
      .add-search{
        color: $brand-primary;
      }

      .added_to_cart {
        color: $brand-primary;
      }
    }
  }

  // 05 Overlay
  &__overlay {
    &-title {
      color: $white;
    }

    &-excerpt {
      color: rgba($white, 0.6);
    }

    &-bg {
      background-color: rgba($white, 0.96);
    }
  }


  // 06 Circle
  &__circle {
    &-front {
      background-image: linear-gradient(
        to top,
        rgba($brand-primary, 0.94),
        rgba($brk-base-2, 0.94)
      );
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
    }
  }
}


// Projects slider
.brs-carousel {
  &__link {
    box-shadow: 3px 4px 15px rgba($brk-secondary-2, 0.4);
  }
}
// Projects slider end

// .slider-thumbnailed-full
.slider-thumbnailed-full {
  &-nav {
    .slick-slide {
      .brk-slid {
        &:after {
          border: 2px solid $white;

          @media (max-width: 575px) {
            border-color: $brand-primary;

            .height-100vh & {
              border-color: $white;
            }
          }
        }
      }
    }
  }
}

// default skin
.slick-dots {
  li {
    button {
      background-color: $brand-primary;
    }
    &:hover,
    &.slick-active {
    }
  }
}

.dots-base-staff-skin {
  .swiper-arrow {
