    background-color: $white;

    &:before {
      color: $brand-primary;
    }
  }
}

.arrows-classic-dark,
.arrows-classic-dark-square,
.arrows-classic-dark-circle {
  .slick-arrow {
    background-color: $brand-primary;

    &:before {
      color: $white;
    }
  }
}

.arrows-dark {
  .slick-prev {
    &:after {
      background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.8),
        rgba(0, 0, 0, 0)
      );
    }

    &:hover {
      &:after {
        opacity: 1;
      }
    }
  }
  .slick-next {
    &:after {
      background-image: linear-gradient(
        to left,
        rgba(0, 0, 0, 0.8),
        rgba(0, 0, 0, 0)
      );
    }

    &:hover {
      &:after {
        opacity: 1;
      }
    }
  }

  .slick-prev,
  .slick-next {
    &:before {
      color: $white;
    }
  }
}

.arrows-modern {
