// brk-pricing
.brk-pricing {
  // colored
  &-colored {
    &__price {
      .period {
        color: $brk-base-2;
      }
    }
    &__btn {
      background-color: $white;
      color: $brand-primary;

      &:before,
      &:after {
        background-color: $brk-base-4;
      }
    }
  }

  // doubled
  &-doubled {
    .pd-icon {
      i {
        color: $brk-base-1;
      }
    }
    .pd-icon-back {
      i {
        color: $white;
      }
    }
    .pb-list {
      i {
        color: $brand-primary;
      }
    }
    .pd-price {
      color: $white;
    }
    .pb-content {
      color: $white;
    }

    &__link {
      color: $white;

      &:hover {
        color: $white;
      }
    }
  }
  // horizontal
  &-horizontal {
    &:before {
      background-image: linear-gradient(to right, $secondary, $brand-primary);
    }
    &:hover {
      .brk-pricing-horizontal__viewed {
        span {
          color: $white;
        }
      }
      .brk-pricing-horizontal__price {
        color: $white;
      }
      .brk-pricing-horizontal__list-options {
        .options-active {
          color: $white;

          i {
            color: $white; // base
          }
        }
        .options-deactive {
          color: #c29aef; // base

          i {
            color: rgba($white, 0.4); // base
          }
        }
      }
      .h-button {
        box-shadow: 0 0 0 6px rgba($white, 0.14);
      }
    }
    &__viewed {
      background-color: rgba(#000, 0.01); // base
      span {
        color: #999; // deactive
      }
    }
    &__price {
      color: #282b35;
    }
    &__list-options {
      .options-active {
        i {
          color: $brk-secondary-4;
        }
      }
      .options-deactive {
        color: #d9d9d9;
        i {
          color: #d9d9d9;
        }
      }
    }
    .h-button {
      background-color: $white;
      color: $brand-primary;
      box-shadow: 0 0 0 2px $brand-primary,
        0 5px 16px rgba($brand-primary, 0.5);
      &:hover {
        color: $white;
      }
    }
  }
  // rounded
  &-rounded {
    &:hover {
      .brk-pricing-rounded__content {
        box-shadow: 0 0 0 3px rgba($brand-primary, 0.75);
      }
    }
    &__icon {
      i {
        color: $white;
      }
      .before {
        background-color: $brand-primary;
      }
      .after {
        background-color: $brand-primary;
      }
    }
    &__content {
      background-color: $white;
    }
    .rd-view {
      &__title {
        color: $brand-primary;
      }
      &__price {
        .old-price {
          color: $brand-primary;
        }
      }
    }
    .rd-description {
      &__list {
        li {
          &:nth-child(2n) {
            background-color: #f8fafe;
          }
        }
      }
    }
    .rd-circl-overlay {
      background-color: #f8fafe;
    }
  }
  // strict
  &-strict {
    &:hover {
      .brk-pricing-strict__title {
        color: $white;
      }
    }
    &__content {
      background-color: $white;
    }
    &__title {
      background-color: $brk-base-4;
      color: $brand-primary;
    }
    &__price {
      .st-sign {
        color: $brand-primary;
      }
    }
    &__description {
      li {
        &:nth-child(2n-1) {
          background-color: #f8fafe;
        }
      }
    }
  }

  // shop
  &-shop {
    border-bottom: 3px solid $brand-primary;

    &:hover {
      .brk-pricing-shop__view {
        background-color: $brand-primary;
      }
      .brk-pricing-shop__title {
        color: $white;
      }
      .price-item {
        color: $white;
      }
      .price-icon {
        background-color: $white;
        i {
          color: $brand-primary;
        }
        .before {
          background-color: rgba($white, 0.14);
        }
        .after {
          background-color: rgba($white, 0.24);
        }
      }
    }

    &__title {
      color: $brand-primary;
    }
    .price-icon {
      background-color: $brand-primary;
      .before {
        background-color: rgba($brand-primary, 0.05);
      }
    }
    .brk-pricing-shop__block-info {
      border-top: 1px solid #e9f0fb;
      background-color: #f7fafe;
    }
    .brk-pricing-shop__btn {
      background-color: $white;
      color: $brand-primary;

      &:before,
      &:after {
        background-color: $brand-primary;
      }

      &:hover {
        color: $white;
      }
    }
  }
}

