/* pagination */
.brk-pagination {
  // one
  &-one {
    .pagination {
      background-color: $white; //BASE
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12); //BASE
    }
    .page-numbers {
      &:after {
        background-color: $brand-primary; // BASE
      }
      &:hover {
        color: $brand-primary; //BASE
      }
      &.current {
        color: $brand-primary; //BASE
      }
      &.prev,
      &.next {
        &:before {
          color: $brand-primary; //BASE
        }
      }
    }
  }
  // two
  &-two {
    .pagination {
      background-color: $white; //BASE
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12); //BASE
    }
    .page-numbers {
      color: #dfdfdf; //BASE
      &:hover {
        color: $brand-primary; //BASE
      }
      &.current {
        color: $brk-dark-base; //BASE
      }
      &.prev,
      &.next {
        &:before {
          color: $brand-primary; //BASE
        }
      }
    }
  }

  // three
  &-three {
    .pagination {
      background-color: #2b2c2d; //BASE
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12); //BASE
    }
    .page-numbers {
      color: $white; //BASE
      border-left: 1px solid #444546; //BASE
      &:nth-last-child(2) {
        border-right: 1px solid #444546; //BASE
      }
      &:after {
        background-color: $brand-primary; //BASE
      }
      &:hover,
      &.current {
        color: $white; //BASE
        background-color: #444546; //BASE
      }
      &.prev,
      &.next {
        &:before {
          color: $brand-primary; //BASE
        }
      }
    }
  }

  // four
  &-four {
    .nav-links {
      &:hover .page-numbers:not(:hover) {
        &.current {
          color: $brand-primary; //BASE
        }
      }
    }
    .page-numbers {
      color: $brk-dark-base; //BASE
      &:after {
        border: 2px solid $brand-primary; //BASE
        box-shadow: 0 5px 16px rgba($white, 0.1); //BASE
      }
      &.current {
        &:hover {
          color: $brk-dark-base; //BASE
        }
      }
      &.prev,
      &.next {
        &:before {
          color: $brk-dark-base; //BASE
        }
      }
    }
  }

  // five
  &-five {
    .page-numbers {
      color: $brk-dark-base; //BASE
      &:after {
        border: 6px solid $brand-primary; //BASE
      }
      &:not(.dots):hover {
        border-color: $brand-primary; //BASE
      }
      &.current {
        border-color: $brand-primary; //BASE
      }
      &.prev,
      &.next {
        background-color: $brand-primary; //BASE
        &:before {
          color: $white; //BASE
        }
        &:hover {
          &:before {
            color: $brk-dark-base; //BASE
          }
        }
      }
    }
  }

  // six
  &-six {
    .pagination {
      background-color: $white; //BASE
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12); //BASE
    }
    .page-numbers {
      color: $brk-dark-base; //BASE
      &:after {
        background-color: rgba($brand-primary, 0.8); //BASE
      }
      &.current {
        color: $brand-primary; //BASE
      }
      &.prev,
      &.next {
        border: 2px solid rgba(39, 39, 39, 0.06); //BASE
        &:before {
          color: $brk-dark-base; //BASE
        }
        &:hover {
          &:before {
            color: $brand-primary; //BASE
          }
        }
      }
    }
  }

  // seven
  &-seven {
    .page-numbers {
      color: $brk-dark-base; //BASE
      border: 1px solid #ececec; //BASE
      &:nth-last-child(2) {
        border-right-color: #ececec; //BASE
      }
      &:hover {
        color: $white; //BASE
        background-color: $brand-primary; //BASE
        border: 1px solid $brand-primary; //BASE
        border-right-color: $brand-primary; //BASE
      }
      &.current {
        color: $white; //BASE
        background-color: $brand-primary; //BASE
        box-shadow: 0 5px 16px rgba(39, 117, 255, 0.5); //BASE
        border: 1px solid $brand-primary; //BASE
        border-right-color: $brand-primary; //BASE
      }
      &.prev,
      &.next {
        border-right-color: #ececec; //BASE
        &:before {
          color: $brk-dark-base; //BASE
        }
        &:hover {
          border-right-color: $brand-primary; //BASE
          &:before {
            color: $white; //BASE
          }
        }
      }
    }
  }

  // eight
  &-eight {
    .nav-links {
      border: 1px solid #ececec; //BASE
      &:hover .page-numbers:not(:hover) {
        &.current {
          color: $brand-primary; //BASE
        }
      }
    }
    .page-numbers {
      color: $brk-dark-base; //BASE
      &:after {
        background-color: $brand-primary; //BASE
        box-shadow: 0 5px 16px rgba(39, 117, 255, 0.5); //BASE
      }
      &:hover {
        color: $white; //BASE
      }
      &.current {
        color: $white; //BASE
      }
      &.dots {
        &:hover {
          color: $brk-dark-base; //BASE
        }
      }
      &.prev,
      &.next {
        border: 1px solid #ececec; //BASE
        &:before {
          color: $brk-dark-base; //BASE
        }
        &:hover {
          border-color: $brand-primary; //BASE
          &:before {
            color: $white; //BASE
          }
        }
      }
    }
  }

  // center
  &-center {
    .brk-prev-link,
    .brk-next-link {
      border: 1px solid #ececec; //BASE
      &:hover {
        background-color: $brand-primary; //BASE
        border-color: $brand-primary; //BASE
        box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE
        color: $white; //BASE
      }
    }
  }

  // large
  &-large {
    &:before {
      background-color: #ececec; //BASE
    }
    .brk-prev-link,
    .brk-next-link {
      background-color: $white; //BASE
      border: 1px solid #ececec; //BASE
      &:hover {
        background-color: $brand-primary; //BASE
        border-color: $brand-primary; //BASE
        box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE
        color: $white; //BASE
      }
    }
  }
}

