/* testimonials */

/* Form controls */
.brk-form {
  /* Strict */
  &-strict {
    // input
    [type="text"],
    [type="password"],
    [type="email"],
    [type="tel"] {
      border-bottom: 1px solid $brk-base-4 !important; //BASE
      color: $brk-dark-base; //BASE
      + .input-label {
        color: rgba($brk-dark-base, 0.4); //BASE
      }
      &[disabled] {
        border-bottom: 1px solid #e9e9e9; //BASE
      }
    }

    // textarea
    textarea {
      border: 1px solid $brk-base-4; //BASE
      + .input-label {
        color: rgba($brk-dark-base, 0.4); //BASE
      }
    }

    // select
    .jq-selectbox {
      &__select {
        border-bottom: 1px solid $brk-base-4; //BASE
        color: rgba($brk-dark-base, 0.4); //BASE
      }

      &__trigger {
        &-arrow {
          border-top: 6px solid $brand-primary; //BASE
        }
      }

      &__dropdown {
        background-color: $white; //BASE
        border: 1px solid $brand-primary; //BASE
        ul {
          li {
            color: rgba($brk-dark-base, 0.4); //BASE
            &.sel {
              color: #d4e3ff;
            }
            &:hover,
            &.selected {
              color: $brand-primary;
            }
          }
        }
      }

      &.opened {
        .jq-selectbox {
          &__select {
            border-bottom-color: $brand-primary; //BASE

            &-text {
              color: $brk-dark-base;
            }
          }
        }
      }
    }

    // multiselect
    .jq-select-multiple {
      ul {
        border: 1px solid $brk-base-4; //BASE
        &:hover {
          border-color: $brand-primary; //BASE
        }
        li {
          color: rgba($brk-dark-base, 0.4); //BASE

          &:hover,
          &.selected {
            color: $brand-primary; //BASE
          }
        }
      }
    }

    // File
    [type="file"] {
      ~ .file-info {
        border-bottom: 1px solid $brk-base-4; //BASE
        color: rgba($brk-dark-base, 0.4); //BASE
        &:focus,
        &:active {
          border-bottom-color: $brand-primary; //BASE
        }
      }
      ~ .icon-before {
        i {
          color: $brand-primary;
        }
      }
    }

    // Date
    .brk-form-date-wrap {
      input {
        border-bottom: 1px solid $brk-base-4 !important; //BASE
        color: $brk-dark-base; //BASE
        @include placeholder(rgba($brk-dark-base, 0.4)); //BASE
        &:focus,
        &:active {
          border-bottom-color: $brand-primary !important; //BASE
        }
        ~ .icon-before {
          i {
            color: $brand-primary; //BASE
          }
        }
      }
    }

    // checkbox
    .brk-form-checkbox {
      .checkbox-custom {
        border: 1px solid #e7e7e7; //BASE
        background-color: $white; //BASE
        i {
          color: $white; ///BASE
        }
      }
      input:checked + .checkbox-custom {
        border-color: $brand-primary; //BASE
        background-color: $brand-primary; //BASE
      }
      &-label {
        color: $brk-dark-base; //BASE
      }
    }

    // radio
    .brk-form-radio {
      .radio-custom {
        border: 1px solid #e7e7e7; //BASE
        background-color: $white; //BASE
        &:before {
          background-color: $brand-primary;
        }
      }
      input:checked + .radio-custom {
        border-color: $brand-primary; //BASE
      }
      &-label {
        color: $brk-dark-base; //BASE
      }
    }
  }

  &-wrap {
    &-active {
      [type="text"],
      [type="password"],
      [type="email"],
      [type="tel"] {
        border-bottom-color: $brand-primary !important; //BASE
      }

      textarea {
        border: 1px solid $brand-primary; //BASE
      }
    }
  }
  /* End Strict */

  /* Round */
  &-round {
    // input
    [type="text"],
    [type="password"],
    [type="email"],
    [type="tel"] {
      background-color: $white; //BASE
      border: 2px solid rgba(205, 205, 205, 0.2); //BASE
      color: $brk-dark-base; //BASE
      @include placeholder(rgba($brk-dark-base, 0.4)); //BASE

      &:active,
      &:focus {
        box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
        border-color: $brk-base-2; //BASE
      }
      &[disabled] {
        border: 2px solid rgba(205, 205, 205, 0.2); //BASE
        background-color: #f6f6f6; //BASE
        &:active,
        &:focus {
          border-color: rgba(205, 205, 205, 0.2);
        }
      }
      &[readonly] {
        &:active,
        &:focus {
          border-color: rgba(205, 205, 205, 0.2); //BASE
        }
      }
    }

    // textarea
    textarea {
      border: 2px solid rgba(205, 205, 205, 0.2); //BASE
      color: $brk-dark-base; //BASE
      @include placeholder(rgba($brk-dark-base, 0.4)); //BASE
      &:active,
      &:focus {
        box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
        border-color: $brk-base-2; //BASE
      }
    }

    // select
    .jq-selectbox {
      &__select {
        border: 2px solid rgba(205, 205, 205, 0.2); //BASE
        color: rgba($brk-dark-base, 0.4); //BASE
      }
      &__trigger {
        &-arrow {
          border-top: 5px solid $brk-base-2;
          &:before {
            border-top: 5px solid $white;
          }
        }
      }

      &__dropdown {
        background-color: $white;
        border: 2px solid $brk-base-2; //BASE
        box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
        &:before {
          background-color: $white;
        }
        ul {
          li {
            color: rgba($brk-dark-base, 0.4); //BASE

            &.sel {
              color: $brk-base-2; //BASE
            }

            &:hover,
            &.selected {
              color: $brk-base-2; //BASE
            }
          }
        }
      }

      &.opened {
        .jq-selectbox {
          &__select {
            border-color: $brk-base-2; //BASE
            &-text {
              color: $brk-dark-base;
            }
          }
        }
      }
    }

    // multiselect
    .jq-select-multiple {
      border: 2px solid rgba(205, 205, 205, 0.2); //BASE
      color: rgba($brk-dark-base, 0.4); //BASE

      &:hover {
        border-color: $brk-base-2; //BASE
        box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
      }
      ul {
        li {
          color: rgba($brk-dark-base, 0.4); //BASE

          &:hover,
          &.selected {
            color: $brand-primary;
          }
        }
      }
    }

    // File
    [type="file"] {
      ~ .file-info {
        background-color: $white; //BASE
        border: 2px solid rgba(205, 205, 205, 0.2); //BASE
        color: rgba($brk-dark-base, 0.4); //BASE
      }

      ~ .icon-before {
        i {
          color: $brk-base-2;
        }
      }
    }

    // Date
    .brk-form-date-wrap {
      input {
        background-color: $white; //BASE
        border: 2px solid rgba(205, 205, 205, 0.2); //BASE
        color: $brk-dark-base; //BASE
        @include placeholder(rgba($brk-dark-base, 0.4));

        &:active,
        &:focus {
          box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
          border-color: $brk-base-2; //BASE
        }
        + .icon-before {
          background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
          i {
            color: $white; //BASE
          }
        }
      }
    }

    // checkbox
    .brk-form-checkbox {
      .checkbox-custom {
        border: 1px solid #e7e7e7; //BASE
        background-color: $white; //BASE
        i {
          color: $white;
        }
      }

      input:checked + .checkbox-custom {
        background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
      }

      &-label {
        color: $brk-dark-base;
      }
    }

    // radio
    .brk-form-radio {
      .radio-custom {
        border: 1px solid #e7e7e7; //BASE
        background-color: $white; //BASE
        &:before {
          background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
        }
      }
      input:checked + .radio-custom {
        border-color: $brk-base-2; //BASE
      }

      &-label {
        color: $brk-dark-base;
      }
    }
  }
  /* End Round */

  /* Transparent */
  &-transparent {
    // input
    [type="text"],
    [type="password"],
    [type="email"],
    [type="tel"] {
      color: $white; //BASE
      border: 2px solid rgba(255, 255, 255, 0.2);

      &:active,
      &:focus {
        border-color: $white; //BASE
      }
      &[disabled] {
        border: 2px solid rgba(255, 255, 255, 0.1); //BASE
        background-color: rgba($white, 0.04);
        &:active,
        &:focus {
          border-color: rgba(255, 255, 255, 0.1);
        }
      }
    }

    // textarea
    textarea {
      border: 2px solid rgba(255, 255, 255, 0.2); //BASE
      background: rgba(255, 255, 255, 0.12);
      color: $white; //BASE
      @include placeholder(rgba($white, 0.4));
      &:active,
      &:focus {
        border-color: $white; //BASE
      }
    }

    // select
    .jq-selectbox {
      &__select {
        border: 2px solid rgba(255, 255, 255, 0.2); //BASE
        color: rgba($white, 0.4); //BASE
        background: rgba(255, 255, 255, 0.12);
      }

      &__trigger {
        &-arrow {
          border-top: 5px solid $white;
        }
      }

      &__dropdown {
        background-color: $white;
        border: 2px solid $white; //BASE
        box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1); //BASE

        ul {
          li {
            color: rgba($brk-dark-base, 0.4); //BASE
            &.sel {
              color: $brk-base-2; //BASE
            }
            &:hover,
            &.selected {
              color: $brk-base-2; //BASE
            }
          }
        }
      }
      &.opened {
        .jq-selectbox {
          &__select {
            border-color: $white; //BASE
            background-color: $white;
            &-text {
              color: $brk-dark-base;
            }
          }
          &__trigger {
            &-arrow {
              border-top: 5px solid $brk-dark-base;
            }
          }
        }
      }
    }

    // multiselect
    .jq-select-multiple {
      border: 2px solid rgba(255, 255, 255, 0.2); //BASE
      color: rgba($white, 0.4); //BASE
      background: rgba(255, 255, 255, 0.12);
      box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1); //BASE
      &:hover {
        border-color: $white; //BASE
      }
      ul {
        li {
          color: rgba($white, 0.4); //BASE
          &:hover,
          &.selected {
            color: $white;
          }
        }
      }
    }

    // File
    [type="file"] {
      ~ .file-info {
        background: rgba(255, 255, 255, 0.12); //BASE
        border: 2px solid rgba(255, 255, 255, 0.2); //BASE
        color: rgba($white, 0.4); //BASE
      }
      ~ .icon-before {
        background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
        i {
          color: $white; //BASE
        }
      }
    }

    // Date
    .brk-form-date-wrap {
      input {
        background: rgba(255, 255, 255, 0.12); //BASE
        border: 2px solid rgba(255, 255, 255, 0.2) !important; //BASE
        color: $white; //BASE
        @include placeholder(rgba($white, 0.4));
        &:active,
        &:focus {
          border-color: $white; //BASE
        }
        + .icon-before {
          background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
          i {
            color: $white; //BASE
          }
        }
      }
    }

    // checkbox
    .brk-form-checkbox {
      .checkbox-custom {
        border: 2px solid rgba(255, 255, 255, 0.2); //BASE
        background: rgba(255, 255, 255, 0.12); //BASE
        i {
          color: $white;
        }
      }
      input:checked + .checkbox-custom {
        border-color: $white;
      }
      &-label {
        color: $white;
      }
    }

    // radio
    .brk-form-radio {
      .radio-custom {
        border: 2px solid rgba(255, 255, 255, 0.2); //BASE
        background: rgba(255, 255, 255, 0.12); //BASE
        i {
          color: $white;
        }
      }

      input:checked + .radio-custom {
        border-color: $white; //BASE
      }

      &-label {
        color: $white;
      }
    }
  }
  /* End Transparent */
}

/* End Form controls */

/* Google Maps */
.brk-map {
  &__layer-top,
  &__layer-bottom {
    fill: $white; //BASE
  }

  &__opener {
    &-section {
      &_classic {
        background-color: rgba($white, 0.8); //BASE
        &:hover {
          background-color: $white; //BASE
        }
      }
    }
    &_classic {
      .brk-map-second {
        i {
          &.fa-map-marker {
            color: $brand-primary; //BASE
          }
        }
      }
    }

    &_simple {
      background-color: $brand-primary; //BASE
      box-shadow: 0 5px 16px rgba(39, 117, 255, 0.5); //BASE
      color: $white; //BASE
      span {
        border-bottom: 1px dotted $white; //BASE
      }
      .trigger {
        background-color: $white; //BASE
        i {
          color: $brand-primary; //BASE
        }
      }
    }

    &_round {
      background-color: $brand-primary; //BASE
      box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE
      color: $white; //BASE
    }
  }
  &__infoicon {
    // Layout #1
    &_layout-one {
      background-color: $white; //BASE
      &:before {
        background-image: linear-gradient(
          to right,
          rgba($brand-primary, 0.94),
          rgba($brk-secondary-3, 0.94)
        ); //BASE
      }
      .brk-map__infoicon--text {
        ul {
          color: $brand-primary; //BASE
        }

        a {
          &:hover {
            color: $black; //BASE
          }
        }
      }

      .btn {
        box-shadow: 0 5px 16px rgba($brand-primary, 0.5); ///BASE
      }
    }

    // Layout #2
    &_layout-two {
      &:before {
        background-color: rgba($brand-primary, 0.96); //BASE
      }

      .brk-map__infoicon--text {
        h4 {
          color: $white; //BASE
        }

        p {
          color: rgba($white, 0.8); //BASE
        }

        a {
          color: rgba($white, 0.8); //BASE

          &:hover {
            color: rgba($white, 0.8); //BASE
          }
        }
      }
    }

    // Layout #3
    &_layout-three {
      background-color: $white; //BASE

      .marker {
        background-color: $brand-primary; //BASE
        &:before {
          border-right: 13px solid $brand-primary; //BASE
        }
        i {
          color: $white; //BASE
        }

        svg {
          path,
          circle,
          line,
          polyline,
          rect {
            stroke: $white; //BASE
          }
        }
      }

      .brk-map__infoicon--text {
        p {
          color: #9f9f9f; //BASE
        }
        a {
          color: $brand-primary; //BASE

          span {
            background-color: $brand-primary; //BASE
            box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE

            i {
              color: $white; //BASE
            }
          }
        }
      }
    }

    // Layout #4
    &_layout-four {
      &:before {
        border: 9px solid $brand-primary; //BASE
      }

      &:after {
        border-right: 13px solid $brand-primary; //BASE
      }

      .brk-map__infoicon--text {
        background-color: $gray-dark; //BASE
        h4,
        ul {
          color: $white; //BASE
        }
      }
    }

    // Layout #5
    &_layout-five {
      background-color: $white; //BASE
      border: solid 3px $brand-primary; //BASE

      &:before {
        border: 9px solid $brk-base-3; //BASE
      }

      .marker {
        background-color: $brand-primary; //BASE
        background-image: linear-gradient(
          to top,
          rgba(255, 255, 255, 0),
          rgba(255, 255, 255, 0.14)
        ); //BASE
      }
    }

    // Layout #6
    &_layout-six {
      background-color: $white; //BASE
      border: solid 2px $brand-primary; //BASE

      &:before {
        border-top: 11px solid $brand-primary; //BASE
      }
    }
  }

  // Subscribe
  &__subscribe {
    color: $white; //BASE
    .brk-subscribe {
      border-top: 1px solid rgba($white, 0.2); //BASE
      border-bottom: 1px solid rgba($white, 0.2); //BASE
    }
    &--contacts {
      li {
        span,
        a {
          color: $white; //BASE
        }
      }
    }
  }
}

.brk-subscribe {
  [type="email"] {
    background-color: $white; //BASE
    box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5); //BASE
    color: $brk-base-3; //BASE
    @include placeholder($brk-base-3); //BASE
  }

  button,
  [type="submit"] {
    background-color: $white; //BASE
    box-shadow: 0 5px 16px rgba(39, 117, 255, 0.5); //BASE

    i {
      color: $brand-primary; //BASE
    }
  }
}

/* End Google Maps */

/* Tiles */
.brk-tiles {
  // Full
  &-full {
    &:hover {
      .brk-tiles-full__content {
        box-shadow: 0 6px 30px rgba($brand-primary, 0.6); //BASE

        &:before {
          border-right-color: $brk-base-1; //BASE
          border-left-color: $brk-base-2; //BASE

          @media (max-width: 767px) {
            border-bottom-color: $brk-base-2; //BASE
          }
        }

        h4,
        p {
          color: $white; //BASE
        }

        h4 {
          &:before {
            background-color: $white; //BASE
          }
        }

        .brk-tiles-full__link {
          border: 2px solid $white; //BASE
        }
      }
    }

    // Left //
    &_left {
      .brk-tiles-full__content {
        &:before {
          border-left: 16px solid $white; //BASE

          @media (max-width: 767px) {
            border-bottom: 16px solid $white; // BASE
          }
        }
      }
    }
    // End Left //

    &__thumb {
      &:before {
        background-color: rgba($brk-base-2, 0.2); //BASE
      }
    }

    &__content {
      &:before {
        border-right: 16px solid $white; // BASE

        @media (max-width: 767px) {
          border-bottom: 16px solid $white; // BASE
        }
      }

      &:after {
        background-image: linear-gradient(
          50deg,
          rgba($brand-primary, 0.94),
          rgba($brk-base-2, 0.94)
        ); //BASE
      }

      h4 {
        &:before {
          background-color: $brand-primary; //BASE
        }
      }

      p {
        color: #9f9f9f; //BASE
      }
    }

    &__link {
      background-color: $white; //BASE
      border: 2px solid #f4f4f4; //BASE

      i {
        color: $brand-primary; //BASE
      }
    }
  }

  // Simple
  &-simple {
    &__content {
      h4 {
        color: $white; //BASE
      }

      p {
        color: #c5c5c5; //BASE
      }
    }

    &__link {
      background-color: $brand-primary; //BASE
      box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE

      .before {
        background-color: rgba($brand-primary, 0.5); //BASE
      }

      .after {
        background-color: rgba($brand-primary, 0.5); //BASE
      }

      i {
        color: $white; //BASE
      }
    }
  }

  // Clean
  &-clean {
    background-color: $white; //BASE
    border-top: 1px solid #e9e9e9; //BASE
    border-bottom: 1px solid #e9e9e9; //BASE

    &__thumb {
      &:before {
        background-color: rgba($brand-primary, 0.6); //BASE
      }
    }

    &__content {
      background-color: $white; //BASE

      &:before {
        background-color: $white; //BASE
      }

      p {
        color: #9f9f9f; //BASE
      }
    }

    &__link {
      background-color: $brand-primary; //BASE
      box-shadow: 0 5px 16px rgba($brand-primary, 0.5); //BASE

      i {
        color: $white; //BASE
      }

      span {
        color: $white; //BASE
      }
    }
  }

  // Assorted
  &-assorted {
    &:hover {
      .brk-tiles-assorted {
        &__title {
          box-shadow: 0 6px 30px rgba($brk-secondary-1, 0.6); //BASE

          h3 {
            color: rgba($white, 0.4); //BASE
          }

          h4 {
            color: $white; //BASE
          }
        }

        &__bg {
          box-shadow: 0 6px 30px rgba($brand-primary, 0.6); //BASE
        }
      }
    }

    .before {
      background-color: $white; //BASE
    }

    &__title {
      box-shadow: inset 0 0 0 1px #eee; //BASE

      &:before {
        background-image: linear-gradient(
          50deg,
          rgba($brand-primary, 0.92),
          rgba($secondary, 0.92)
        ); //BASE
      }

      &:after {
        background-color: $white; //BASE
      }

      h3 {
        color: rgba(#000, 0.4); //BASE
      }
    }

    &__bg {
      box-shadow: inset 0 0 0 1px #eee; //BASE

      &:before {
        background-image: linear-gradient(
          50deg,
          rgba($brand-primary, 0.94),
          rgba($brk-base-2, 0.94)
        ); //BASE
      }
    }

    &__link {
      background-color: $white; //BASE

      i {
        color: $brand-primary; //BASE
      }
    }

    &__search {
      i {
        color: $white; //BASE
      }
    }
  }

  // Formats
  &-formats {
    /* formats posts */
    &.format-image {
      .brk-tiles-formats__format-post {
        background-image: linear-gradient(
                        to right,
                        $brk-secondary-4,
                        $brk-secondary-3); //BASE
      }
    }
    &.format-quote {
      .brk-tiles-formats {
        &__format-post {
          background-image: linear-gradient(
                          50deg,
                          $brand-primary,
                          $brk-base-2); //BASE
        }
      }
    }
    &.format-gallery {
      .brk-tiles-formats__format-post {
        background-image: linear-gradient(
                        to right,
                        $brk-secondary-5,
                        $brk-secondary-6); //BASE
      }
    }
    &.format-standard {
      .brk-tiles-formats {
        &__format-post {
          background-image: linear-gradient(
            to right,
            #50c2ae,
            #02d8b7,
            #0cdfbf
          ); //BASE
        }
      }
    }
    /* end formats posts */

    &__format-post {
      color: $white; //BASE
    }

    &:after {
      background-color: rgba(#000, 0.6); ///BASE
    }

    &:before {
      background-image: linear-gradient(
        50deg,
        rgba($brand-primary, 0.86),
        rgba($brk-base-2, 0.86)
      ); //BASE
    }

    /* Content */
    &__content {
      color: $white; //BASE

      h4 {
        color: $white; //BASE
      }
    }

    &__read-more {
      background-color: $white; //BASE
      color: $brand-primary; //BASE
    }
  }
}
/* End Tiles */

