/* Timelines */

.timeline {
  .swiper-scrollbar-drag {
    &:after {
      background-image: linear-gradient(
                      -97deg,
                      $brk-secondary-6,
                      $brk-secondary-3) !important;
    }
  }

  .swiper-pagination-bullet {
    background: transparent;
  }

  .swiper-pagination-bullet-active {
    &:after {
      background: $brand-primary;
    }
  }

  &--masonry {
    .swiper-progress__bar {
      background-image: linear-gradient(
        to left,
        rgba($brk-base-3, 1),
        rgba($brk-secondary-3, 1)
      );
    }
  }

  &--strict,
  &--masonry {
    .swiper-pagination-bullet {
      &:after {
        background: #d5d5d5;
      }
    }
    .swiper-pagination-bullet-active {
      &:after {
        background: $brand-primary;
      }
    }

    .swiper-progress__bar {
      background: $brand-primary;
    }
  }

  &--vertical-circles,
  &--vertical-squares {
    .timeline {
      &__item {
        &:before {
          background: $brand-primary;
          box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
        }
        &:after {
          background-image: repeating-linear-gradient(
            90deg,
            transparent,
            transparent 7px,
            $brand-primary 7px,
            $brand-primary 12px
          );
        }
      }
    }
  }

  &--vertical-squares {
    .timeline {
      &__item {
        &:before {
          background-image: linear-gradient(to top, $brk-base-2, $brk-base-5);
          box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
        }

        &:after {
          background: #ebebeb;
        }

        .timeline__active-line{
          background: $brk-base-5;
        }
      }
    }
  }
}

