/* brk-sc-flip */
.brk-sc-flip {

  &-one {
    &__front,
    &__back {
      background-color: #ededed; //BASE
    }

    /* Components */
    &__stick {
      &_style-1 {
        &:before {
          background-color: $brand-primary; //BASE
        }
        &:after {
          border-left-color: $brand-primary - #111 !important; //BASE
          border-top-color: $brand-primary - #111 !important; //BASE
        }
      }

      &_style-2 {
        &:before {
          background-color: $brk-base-5; //BASE
        }
        &:after {
          border-left-color: $brk-base-5 - #111 !important; //BASE
          border-top-color: $brk-base-5 - #111 !important; //BASE
        }
      }
    }

    &__description {
      background-color: $white; //BASE
    }
    /* Components */
  }

  &-two {

    &__front,
    &__back {
      background-color: $white; //BASE
    }

    &__back {
      &:before,
      &:after {
        background-color: $white; //BASE
      }
    }
  }

  &-three {

    &__front {
      background-color: #ededed; //BASE
    }

    /* Components */
    &__stick {

      &:before,
      &:after {
        background-color: $brk-base-3; //BASE
      }
    }

    &__description {

      .brk-rating__layer,
      .brk-rating__imposition {
        i {
          color: $white;
        }
      }
    }
    /* Components */
  }

  &-four {
    &__front,
    &__back {
      background-color: #f5f5f5; //BASE
    }

    /* Components */
    &__stick {
      background-color: $brk-secondary-3; //BASE
      color: $white; //BASE

      .brk-sc-flip-four__back & {
        background-color: $white; //BASE
        color: $brand-primary; //BASE
      }
    }

    &__title {
      .brk-sc-flip-four__back & {
        color: $white; //BASE
      }
    }

    &__old-price {
      color: #c4c4c4; //BASE
    }

    &__description {
      color: $white; //BASE
    }
    /* Components */
  }
}
/* brk-sc-flip */

/* brk-sc-tiles */
.brk-sc-tiles {

  &-trend { /* brk-sc-tiles-trend */
    &_white-text { /* mod: white-text */

      .brk-sc-tiles-trend__content {
        box-shadow: 0 5px 10px rgba(0,0,0,.08), inset 0 -3px $brk-secondary-3; //BASE
      }

      .brk-sc-tiles-trend__title {
        color: $white; //BASE
      }

      .brk-sc-tiles-trend__text {
        color: rgba($white, .6); //BASE
      }

      .brk-sc-tiles-trend__price-buy {
        &--price {
          color: $white; //BASE
        }
      }
    } /* mod: white-text */

    &_dark-text { /* mod: dark-text */
      &:before {
        background-color: rgba($white, .88); //BASE
      }

      .brk-sc-tiles-trend__text {
        color: rgba($brand-primary, .6); //BASE
      }
    } /* mod: dark-text */
  } /* brk-sc-tiles-trend */

  &-images { /* brk-sc-tiles-images */
    &:before {
      background: linear-gradient(to right, rgba(255,255,255,0.98) 0%,rgba(255,255,255,0.7) 58%,rgba(255,255,255,0.3) 67%,rgba(255,255,255,0) 100%); //BASE

      @media (max-width: 767px) {
        background-color: rgba($white, .7); //BASE
      }
    }

    &__year {
      &:before {
        .brk-sc-tiles-images_style-1 & {
          background-image: linear-gradient(
                          to right,
                          $brk-base-3,
                          $brk-secondary-3); //BASE
        }

        .brk-sc-tiles-images_style-2 & {
          background-image: linear-gradient(
                          to right,
                          $brk-base-2,
                          $brk-base-5); //BASE
        }
      }
    }

    &__text {
      color: #868686; //BASE

      @media (max-width: 767px) {
        color: #5a5a5a; //BASE
      }
    }

    &__btn {
      .brk-sc-tiles-images_style-2 & {
        .border-btn {
          border-color: $brk-base-2; //BASE
        }
      }
    }

  } /* brk-sc-tiles-images */

  &-split { /* brk-sc-tiles-split */
    &__product {
      background-color: #f5f5f5; //BASE
    }

    &__btn {
      .brk-sc-tiles-split_style-2 & {
        &.btn-gradient {
          background-image: linear-gradient(
                          to top,
                          $brk-base-2,
                          $brk-base-5); //BASE

          > .after {
            background-image: linear-gradient(
                            to bottom,
                            $brk-base-2,
                            $brk-base-5); //BASE
          }
        }
      }
    }
  } /* brk-sc-tiles-split */

  &-angle { /* brk-sc-tiles-angle */
    &__loading {
      color: $white; //BASE

      &:before {
        border: 3px solid $white; //BASE
        background-color: $brand-primary; //BASE
      }
    }
  } /* brk-sc-tiles-angle */
}
/* brk-sc-tiles */

