    /* Pagination */

    &__desc {
      &--text {
        color: rgba($white, .6); //BASE

        @media (max-width: 767px) {
          color: rgba($white, .8); //BASE
        }
      }
    }
  }

  &-four {

    &__desc {
      &--text {
        color: $brk-base-7; //BASE
        border-top: 1px solid rgba($white, .2); //BASE
      }
    }

    /* Pagination */
    &-pagination {
      &-bullet {

        &:before {
          background-color: $white; //BASE
        }

        &-active {
          border: 1px solid #dbdbdb !important; //BASE
        }
      }
    }
    /* Pagination */
  }
}

.brk-sc-angle-post {
  &__container {
    background-color: #d1d1d1; //BASE
  }
  &__price {
    box-shadow: 0 5px 16px rgba($brand-primary,.5); //BASE
  }

  &__buy {
    box-shadow: 0 5px 16px rgba($brand-primary,.5); //BASE
  }
}
/* brk-sc-row */

/* brk-sc-masonry */
.brk-sc-masonry {

  /* One Style */
  &-one {
    &__content {
      &:before,
      &:after {
        background-color: $brk-base-1; //BASE
      }
    }

    /* Components */
    &__link {
      &:before,
      &:after {
        border-bottom: 39px solid $brk-base-1; //BASE
      }
    }
    /* Components */
  }
  /* One Style */

  /* Two Style */
  &-two {
    background-color: $white; //BASE

    &[class*="brk-sc-masonry_narrow"][class*="brk-sc-masonry_small-"] {
      background-color: $black; //BASE

      &:hover {

        .brk-sc-masonry-two__container {
          background-color: #1c2124; //BASE

          &:before {
            background-color: rgba(#252525, .8);  //BASE
          }
        }
        .brk-sc-masonry-two__buy {
          background-color: #303031; //BASE
        }
      }
    }

    /* Components */
    &__links {

      .btn {

        &.btn-prime {
          color: #cdcdcd; //BASE

          &:hover {
            color: #cdcdcd !important; //BASE
          }

          .before,
          .after {
            background-color: rgba(#cdcdcd, .15) !important; //BASE
          }

          .border-btn {
            border-color: #cdcdcd; //BASE
          }
        }

        &.btn-inside-out {

          &:hover {
            background-color: #71a74c !important; //BASE
            color: $white !important; //BASE

            i {
              &:before {
                color: $white !important; //BASE
              }
            }
          }

          .before,
          .after {
            background-color: #8cc54f !important; //BASE
          }
        }
      }

      .wishlist {
        background-color: #f0f1ef; //BASE
      }
    }

    &__buy {
      .btn {
        &:hover {
          color: $white !important; //BASE
        }

        .border-btn {
          border-color: #8cc54f; //BASE
        }

        &.btn-prime:hover > .before,
        &.btn-prime:hover > .after {
          background-color: rgba(#8cc54f, .3) !important; //BASE
        }
      }
    }
    /* Components */
  }
  /* Two Style */

  /* Three Style */
  &-three {
    background-color: $white; //BASE

    &:hover {
      .brk-sc-masonry-three__bottom-content {
        .brk-rating__layer,
        .brk-rating__imposition {
          i {
            color: $white; //BASE
          }
        }
      }

      .brk-sc-masonry-three__title {
        color: $white; //BASE
      }

      .brk-sc-masonry-three__price {
        .old-price,
        .price {
          color: $white; //BASE
        }
      }
    }

    &:before {
      background-color: rgba($brand-primary, .9); //BASE
    }

  }
  /* Three Style */

}
/* brk-sc-masonry */

/* brk-shop-grid-filter */
.brk-shop-grid-filter {

  /* BUTTONS */
  &__button {

    &_style-2 { /* brk-shop-grid-filter__button_style-2 */

      &:before {
        background-color: #f4f4f4; //BASE
      }

      li {
        @media (max-width: 991px) {
          &:before {
            background-color: #f4f4f4; //BASE
          }
        }

        &.checked {
          background-color: $white; //BASE
        }
      }
    } /* brk-shop-grid-filter__button_style-2 */

    &_style-3 { /* brk-shop-grid-filter__button_style-3 */

      li {
        &:before {
          background-image: linear-gradient(to top, rgba(0,0,0,.86), rgba(0,0,0,.43)); //BASE
        }

        &.checked {
          &:before {
            background-image: linear-gradient(to top, rgba(0,0,0,.7), rgba(0,0,0,.07)); ///BASE
          }
        }
      }
    } /* brk-shop-grid-filter__button_style-3 */
  }
  /* BUTTONS */

  /* ITEMS */
  &-grid {

    &:before {
      background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,.2) 100%); //BASE
    }
  }

  &-cells {

    &:before {
      background: linear-gradient(to bottom,  rgba(0,0,0,0.27) 0%,rgba(0,0,0,0.45) 100%); //BASE
    }

    /* Components */
    &__rating {
      .brk-rating {

        &__layer,
        &__imposition {
          i {
            color: $white; //BASE
          }
        }
      }
    }

    &__buy {
      border: solid 2px $white; //BASE

      &:hover {
        background-color: $white; //BASE
      }
    }
    /* Components */
  }

  &-strict {
    background-color: $white; //BASE

    &__back {
      background-color: $white; //BASE
    }

    /* Components */
    &__thumb {
      background-color: #f2f2f2; //BASE
    }

    &__inform {
      .brk-shop-grid-filter-strict__back & {
        border-bottom: 1px solid rgba($brk-dark-base, .1); //BASE
      }
    }

    &__stick {
      &:before {
        background-color: $brand-primary !important; //BASE
      }

      &:after {
        border-left-color: $brand-primary - #111 !important; //BASE
        border-top-color: $brand-primary - #111 !important; //BASE
      }
    }

    &__list {
      border-bottom: 1px solid rgba($brk-dark-base, .1); //BASE
    }

    &__actions {
      a {
        border-left: 1px solid $brand-primary; //BASE
        border-right: 1px solid $brand-primary; //BASE
      }
    }
    /* Components */
  }

  &-special {

    &:before {
      background-image: linear-gradient(to top, rgba(0,0,0,.69), rgba(0,0,0,0) 50%); //BASE
    }

    &:after {
      background-image: linear-gradient(to top, rgba(0,0,0,.86), rgba(0,0,0,.69) 99%); //BASE
    }

    /* Components */
    &__title {
      min-height: 50px;
    }

    &__buy {
      .icon {
        background-color: #fdfdfd; //BASE
      }
    }

    &__stick {
      .stick {

        &:nth-child(1) {
          &:before,
          &:after {
            background-color: #0056a6; //BASE
          }

          span {
            background-image: linear-gradient(
                            to left,
                            $brk-base-6,
                            $brk-base-5); //BASE
          }
        }

        &:nth-child(2) {

          &:before,
          &:after {
            background-color: $brk-secondary-2; //BASE
          }

          span {
            background-image: linear-gradient(
                            to right,
                            $brk-base-3,
                            $brk-secondary-3); //BASE
          }
        }
      }
    }
    /* Components */
  }
  /* ITEMS */
}
/* brk-shop-grid-filter */

